


function App() {
  let f1=(p)=><h3>{p}</h3>;
  
  let ob1={p1:'мой' , p2:'не мой', p3:f1};
  return (
    <div >
     
     
      <C1 {...ob1}/>
      {f2(0,' общественная ')}
    </div>
  );
}

export default App;

let C1=(props)=><div><h3>это компонент C1 {props.p2} а </h3>{props.p3('наш')}</div>
let f2=(p1,p2)=>p1?  <h3>это функция ф2 {p2} </h3>: <h2>это фикция ф2  {p2} </h2>
